import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { Publicacion } from '../../models/publicacion';
import { PublicacionService } from '../../services/publicacion.service';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
export class InicioComponent implements OnInit {
  public publicaciones
  public userPubli
  public img: any
  public sub_img = false
  public img_clou: any
  public img_url: any
  public publicacion = new Publicacion('', '', '', '', '')
  public valid = false
  public valid_publi: any
  public user: any

  constructor(
    private _userService: UserService,
    private _publiService: PublicacionService
  ) {
    this.publicaciones = []
   }

  ngOnInit() {
    this.getPublicaciones()
  }

  getPublicaciones(){
    this._userService.getPublicaciones().subscribe(
      response => {
        this.publicaciones = response
      },
      err => {
        
      }
    )
  }

  getUserPubli(id){
    this._userService.getUser(id).subscribe(
      response => {
        this.userPubli = response
      },
      err => {
        
      }
    )
  }

  publicar(){
    if (this.sub_img) {
      this.publicacion.image = 'subida'
    }
     if(this.publicacion.publicacion == '' && this.sub_img == false){
       this.valid = true
       this.valid_publi = 'LLene algun campo porfavor'
     }else{
       this.valid = true
       this.valid_publi = 'Publicando...'
       this.confirmPublicar()
     }
  }

  confirmPublicar(){
    let id = localStorage.getItem('usl')
      this._userService.getUser(id).subscribe(
        response => {
          this.user = response
          this.publicacion.user = this.user.name
          this.publicacion.img = this.user.image
          this._publiService.addPublicacion(this.publicacion).subscribe(
            response => {
              this.valid_publi = 'Publicacion subida correctamente'
              this.publicacion.publicacion = ''
              this.sub_img = false
              
              this.resetValid()
              this.getPublicaciones()
            },
            err => {
              console.log(err)
            }
          )
        },
        err => {
          console.log(err)
        }
      )
  }

  resetValid(){
    setTimeout(() => {
      this.valid = false
    }, 3000);
  }

  imgPre(e){
    let img = e.target.files[0]
    let formData = new FormData()
    formData.append('file', img)
    formData.append('upload_preset', 'senttito')
    formData.append('cloud_name', 'sennt03')
    this.img = formData
    this.sub_img = true
  }

}
