import { Component, OnInit } from '@angular/core';
import { Publicacion } from '../../models/publicacion';
import { UserService } from '../../services/user.service';

@Component({
  selector: 'add-publication',
  templateUrl: './add-publication.component.html',
  styleUrls: ['./add-publication.component.css']
})
export class AddPublicationComponent implements OnInit {

  public img: any
  public sub_img = false
  public img_clou: any
  public img_url: any
  public publicacion = new Publicacion('', '', '', '', '')

  constructor(
    private _userService: UserService
  ) { }

  ngOnInit() {
  }

  publicacionAdd(){
    if (this.sub_img) {
      this._userService.cloudinary(this.img).subscribe(
        response => {
          console.log(response)
          this.img_clou = response
          this.img_url = this.img_clou.secure_url
          this.publicacion.image = this.img_url
        },
        err => {
          console.log(err)
        }
      )
    }
    return this.publicacion
  }

  imgPre(e){
    let img = e.target.files[0]
    let formData = new FormData()
    formData.append('file', img)
    formData.append('upload_preset', 'senttito')
    formData.append('cloud_name', 'sennt03')
    this.img = formData
    this.sub_img = true
  }

}
