import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { User } from '../../models/user';
import { Router } from '@angular/router';

@Component({
  selector: 'configuracion',
  templateUrl: './configuracion.component.html',
  styleUrls: ['./configuracion.component.css']
})
export class ConfiguracionComponent implements OnInit {
  public user: any
  public valid: any
  public err: any
  public Userimg: any
  public img: any
  public res_img: any
  public url_img: any
  public sub_img = false

  constructor(
    private _userService: UserService,
    private _router: Router
  ) {
    this.user = new User('', '', '', '', '')
  }

  ngOnInit() {
    let id = localStorage.getItem('usl')
    this._userService.getUser(id).subscribe(
      response => {
       this.Userimg = response
      },
      err => {
        console.log(err)
      }
    )
  }

  cancelar(){
    this._router.navigate(['/perfil'])
  }

  guardar(){
    document.querySelector('#btn-guardar').innerHTML = 'Cargando..'
    if(this.user.name){
      this.confirmadoGuardar()
    }else if(this.user.email){
      this.confirmadoGuardar()
    }else if(this.sub_img){
      this.confirmadoGuardar()
    }else{
      this.valid = 'algunCampo'
    }
  }

  confirmadoGuardar(){
    if(this.sub_img){
      this._userService.cloudinary(this.img).subscribe(
      response => {
        this.res_img = response
        this.url_img = this.res_img.secure_url
        this.user.image = this.url_img

        this._userService.editUser(this.user).subscribe(
      response => {
        this.valid = 'confirm'
        document.querySelector('#btn-guardar').innerHTML = 'Guardar'
      },
      err => {
        this.err = err
        this.valid = 'correoFalse'
        document.querySelector('#btn-guardar').innerHTML = 'Guardar'
      }
    )
      },
      err => {
        console.log(err)
      }
    )

    }else{
      this._userService.editUser(this.user).subscribe(
      response => {
        this.valid = 'confirm'
        document.querySelector('#btn-guardar').innerHTML = 'Guardar'
      },
      err => {
        this.err = err
        this.valid = 'correoFalse'
        document.querySelector('#btn-guardar').innerHTML = 'Guardar'
      }
    )
    }
    
  }

  fileChangeEvent(fileInput:any){
    let filesToUpload = fileInput.target.files[0]
    console.log(filesToUpload)
    const formData = new FormData()
        formData.append('file', filesToUpload)
        formData.append('upload_preset', 'senttito')
        formData.append('cloud_name', 'sennt03')
    this.img = formData
    this.sub_img = true
  }

}
